:root {
	--app-height: 100%;
}

body {
	margin: 0;
	font-family: "Noto Serif", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-rendering: optimizeLegibility;
	min-height: 100vh;
	margin-bottom: 100px;
	background: #eee;
	overflow-x: hidden;
}

span,
input {
	font-family: "Noto Sans", sans-serif !important;
}
#couplering-svg {
	z-index: 1;
	background: #eee;
}

.widget-result {
	max-width: 600px;
	margin: 24px;
	font-size: 22px;
	line-height: 38px;
}

h4 {
	max-width: 100% !important;
	font-weight: normal !important;
	font-size: 20px !important;
}

h3 {
	font-size: 26px !important;
	font-size: 26px !important;
	line-height: 30px !important;
}

mark {
	border-radius: 5px;
	padding: 0 10px;
	color: #fff;
	font-weight: bold;
}

mark.indian {
	background: #179a3f;
}
mark.caucasian {
	background: #0893f2;
}
mark.other {
	background: #ff7d00;
}
mark.malay {
	background: #020764;
}
mark.eurasian {
	background: #d28fbc;
}
mark.chinese {
	background: #cc2e2e;
}

mark.groom {
	background: #64ddc6;
}

mark.bride {
	background: #f96093;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.scrollyNode {
	transition: fill 0.5s ease-in-out;
}
.scrollyNodePath {
	transition: fill 0.5s ease-in-out;
}
